// Mock for expo-modules-core to fix ESM import issues in Jest

class EventEmitter {
  addListener() {}
  removeAllListeners() {}
  removeSubscription() {}
}

class UnavailabilityError extends Error {}

class CodedError extends Error {
  constructor(code, message) {
    super(message);
    this.code = code;
  }
}

const mockModule = {
  requireNativeViewManager: () => {},
  NativeModulesProxy: {},
  EventEmitter,
  Platform: {
    OS: 'ios',
  },
  UnavailabilityError,
  CodedError,
};

// Export as both CommonJS and ES Module
module.exports = mockModule;
module.exports.default = mockModule;
module.exports.EventEmitter = EventEmitter;
module.exports.requireNativeViewManager = mockModule.requireNativeViewManager;
module.exports.NativeModulesProxy = mockModule.NativeModulesProxy;
module.exports.Platform = mockModule.Platform;
module.exports.UnavailabilityError = UnavailabilityError;
module.exports.CodedError = CodedError;
